@charset "UTF-8";
@keyframes unyonnyo {
  0% {
    transform: scale(1.08, 0.92);
  }
  50% {
    transform: scale(0.92, 1.08);
  }
  100% {
    transform: scale(1.08, 0.92);
  }
}
@keyframes unyonnyo2 {
  0% {
    transform: scale(0.92, 1.08);
  }
  50% {
    transform: scale(1.08, 0.92);
  }
  100% {
    transform: scale(0.92, 1.08);
  }
}
@keyframes poyopoyo {
  0% {
    top: 6px;
    left: 6px;
  }
  50% {
    top: 0;
    left: 0;
  }
  100% {
    top: 6px;
    left: 6px;
  }
}
@keyframes poyopoyo2 {
  0% {
    top: -7px;
    left: -7px;
  }
  50% {
    top: 0;
    left: 0;
  }
  100% {
    top: -7px;
    left: -7px;
  }
}
.top main {
  margin-top: 0;
}

.top #gu-header-js {
  position: absolute;
  width: 100%;
  z-index: 2;
}

.top .gu-header {
  background: transparent;
}

.top .gu-section::before {
  content: none;
}

.top .gu-container {
  display: flex;
  justify-content: space-between;
  padding: 7.29vw 0;
}
@media screen and (max-width: 767px) {
  .top .gu-container {
    flex-direction: column;
    padding: 18.32vw 0 0;
  }
}

.gu-top-content {
  width: 50%;
}
@media screen and (max-width: 767px) {
  .gu-top-content {
    width: 100%;
    margin-bottom: 16.28vw;
  }
}

.top .gu-button-1 {
  margin-top: 3.28vw;
}
@media screen and (max-width: 767px) {
  .top .gu-button-1 {
    margin-top: 5.85vw;
    width: 28.5vw;
    font-size: 3.05vw;
    padding: 1.02vw 3.05vw;
    line-height: 1.5;
  }
}

.gu-top-inner {
  padding-left: 2.08vw;
}

.gu-top-contentImage {
  width: 100vw;
  max-width: 50%;
  margin: 0 calc(50% - 50vw) 0;
}
@media screen and (max-width: 767px) {
  .gu-top-contentImage {
    max-width: inherit;
  }
}

@media screen and (min-width: 768px) {
  .gu-section:nth-of-type(odd) .gu-top-contentImage {
    order: -1;
  }
}
.gu-top-contentImage img {
  width: 41.67vw;
  display: block;
}
@media screen and (max-width: 767px) {
  .gu-top-contentImage img {
    width: 100%;
  }
}

.gu-heading-3 {
  margin-top: 1em;
}
.gu-heading-3:first-of-type {
  margin-top: 0;
}

@media screen and (min-width: 768px) {
  .gu-vision__content + .gu-button-1 {
    margin-top: 7.81vw;
  }
}

.gu-news-list {
  margin-top: 3.7vw;
  color: #fff;
  font-size: 1.25vw;
  row-gap: 1.67vw;
  display: flex;
  flex-direction: column;
}
@media screen and (max-width: 767px) {
  .gu-news-list {
    row-gap: 9.41vw;
    font-size: 3.05vw;
  }
}
.gu-news-list li a {
  display: flex;
  border-bottom: 1px solid #fff;
  position: relative;
}
.gu-news-list .dt {
  width: 25%;
  text-align: center;
}
.gu-news-list .dd {
  width: 75%;
  padding-right: 3.13vw;
}
@media screen and (max-width: 767px) {
  .gu-news-list .dd {
    padding-right: 7.63vw;
  }
}
.gu-news-list .dd::after {
  content: "→";
  position: absolute;
  right: 1.25vw;
  top: 0;
}
@media screen and (max-width: 767px) {
  .gu-news-list .dd::after {
    right: 3.05vw;
  }
}

.gu-top-online {
  padding: 4.43vw 0 5.52vw;
}
@media screen and (max-width: 767px) {
  .gu-top-online {
    padding: 10.18vw 0;
  }
}
.gu-top-online .gu-heading-1 {
  width: fit-content;
  margin: 0 auto;
  margin-bottom: 2.29vw;
}
@media screen and (max-width: 767px) {
  .gu-top-online .gu-heading-1 {
    margin-bottom: 5.6vw;
  }
}
@media screen and (max-width: 767px) {
  .gu-top-online__image {
    margin-bottom: 10.18vw;
  }
}
.gu-top-online__image img {
  width: 100%;
}

.gu-top-mv {
  height: 56.25vw;
  background: url("/assets/images/top_MV01.jpg") no-repeat 0 0/100%;
  position: relative;
}
@media screen and (max-width: 767px) {
  .gu-top-mv {
    background: url("/assets/images/top_MV01.jpg") no-repeat 0 0/100%;
    height: 100vh;
  }
}
.gu-top-mvTitle {
  position: relative;
  z-index: 10;
  padding-top: 16.67vw;
  text-align: center;
  margin-bottom: 10.94vw;
}
.gu-top-mvTitle__text {
  margin-bottom: 1.77vw;
}
.gu-top-mvScrollGuide {
  width: fit-content;
  margin: 0 auto;
}
.gu-top-mvScrollGuide:after {
  content: "";
  background: #fff;
  margin: 1.35vw auto 0;
  display: block;
  height: 1.77vw;
  width: 1.56vw;
  clip-path: polygon(44% 0, 44% 80%, 0 60%, 0 75%, 50% 100%, 100% 75%, 100% 60%, 56% 80%, 56% 0);
}
.gu-top-mvScrollGuide__text {
  width: 13.33vw;
}
.gu-top-mvSide {
  position: absolute;
  width: 1.46vw;
  top: 35.05vw;
  left: 5.36vw;
}
.gu-top-mvSide__text:after {
  content: "";
  display: block;
  width: 2.6vw;
  height: 2.6vw;
  background: url("/assets/images/icon01.png") no-repeat 0 0/100%;
  margin-top: 1.15vw;
  margin-left: -0.73vw;
}
.gu-top-mvSide img {
  width: 100%;
}

.gu-object__shape {
  position: absolute;
  opacity: 0.6;
  z-index: 1;
}
.gu-object__shape:before, .gu-object__shape:after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}
.gu-object__shape:before {
  top: 5px;
  left: 5px;
}
.gu-object__shape:after {
  top: -5px;
  left: -5px;
}
.gu-object__shape--red:before, .gu-object__shape--red:after {
  background-image: url(/assets/images/object/obje_red01.svg);
}
.gu-object__shape--yellow:before, .gu-object__shape--yellow:after {
  background-image: url(/assets/images/object/obje_yellow01.svg);
}
.gu-object__shape--blue:before, .gu-object__shape--blue:after {
  background-image: url(/assets/images/object/obje_blue01.svg);
}
.gu-object--topMv .gu-object__shape:nth-child(1) {
  top: 8.8vw;
  left: 10%;
  width: 17.19vw;
  height: 18.23vw;
}
@media screen and (max-width: 767px) {
  .gu-object--topMv .gu-object__shape:nth-child(1) {
    top: 61.07vw;
    left: -3%;
    width: 13.99vw;
    height: 13.99vw;
  }
}
.gu-object--topMv .gu-object__shape:nth-child(1):before {
  animation: unyonnyo 9s infinite ease-in-out, poyopoyo 12s infinite ease-in-out;
}
.gu-object--topMv .gu-object__shape:nth-child(1):after {
  animation: unyonnyo2 9s infinite ease-in-out, poyopoyo2 12s infinite ease-in-out;
}
.gu-object--topMv .gu-object__shape:nth-child(2) {
  top: 9.48vw;
  right: 26%;
  width: 10.42vw;
  height: 10.94vw;
}
@media screen and (max-width: 767px) {
  .gu-object--topMv .gu-object__shape:nth-child(2) {
    top: 19.08vw;
    left: 12%;
    width: 22.9vw;
    height: 22.9vw;
  }
}
.gu-object--topMv .gu-object__shape:nth-child(2):before {
  animation: unyonnyo 16s infinite ease-in-out, poyopoyo 24s infinite ease-in-out;
}
.gu-object--topMv .gu-object__shape:nth-child(2):after {
  animation: unyonnyo2 20s infinite ease-in-out, poyopoyo2 24s infinite ease-in-out;
}
.gu-object--topMv .gu-object__shape:nth-child(3) {
  top: 31.25vw;
  right: 13%;
  width: 12.5vw;
  height: 11.88vw;
}
@media screen and (max-width: 767px) {
  .gu-object--topMv .gu-object__shape:nth-child(3) {
    top: 50.89vw;
    left: 70%;
    width: 33.08vw;
    height: 33.08vw;
  }
}
.gu-object--topMv .gu-object__shape:nth-child(3):before {
  animation: unyonnyo 12s infinite ease-in-out, poyopoyo 15s infinite ease-in-out;
}
.gu-object--topMv .gu-object__shape:nth-child(3):after {
  animation: unyonnyo2 12s infinite ease-in-out, poyopoyo2 15s infinite ease-in-out;
}

